<template>
  <div class="settle">
      <div class="detail">
        <p class="check" v-if="hasCheckBox" @click="toggleItem">
          <i ref="checkbox" :class="checked === true ? 'active' : ''"></i>
          <label>全选</label>
        </p>
        <p class="total">
          <label>数量：</label>
          <span>{{total}}</span>
        </p>
        <p class="price">
          <label>价格：</label>
          <span>￥{{price | formatMoney}}</span>
        </p>
      </div>
      <div class="btn" @click="submit">
        {{confirmText}}
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {hasClass,addClass,removeClass} from 'common/js/dom'

  export default {
    data () {
      return {
        checked: false
      }
    },
    filters: {
      formatMoney(value){
        return value.toFixed(2)
      }
    },
    props: {
      confirmText: {
        type: String,
        default: '购物车'
      },
      hasCheckBox: {
        type: Boolean,
        default: false
      },
      total: {
        type: Number,
        default: 0
      },
      price: {
        type: Number,
        default: 0.00
      }
    },
    methods: {
      submit() {
        this.$emit('submit', {
          total: this.total,
          price: this.price
        })
      },
      setChecked(value) {
        this.checked = value
      },
      toggleItem(){
        let checkbox = this.$refs.checkbox
        if(hasClass(checkbox, 'active')){
          this.checked = false
        } else　{
          this.checked = true
        }
      }
    },
    watch: {
      checked(newValue) {
        this.$emit('checked', newValue)
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"

    .settle
      display flex
      width 100%
      height 100%
      .check
        display flex
        height 100%
        align-items center
        background #F0F0F0
        margin-left 6px
        margin-right 6px
        label
          color #4D4D4D
          font-size $font-size-small
          margin-left 4px
        i
          display flex
          background url("~base/good-item/unchecked.png") no-repeat
          background-size contain
          width 13px
          height 13px
          position relative
          &.active
            background url("~base/good-item/checked.png") no-repeat
            background-size contain
          &:before
            position absolute
            left -10px
            top -10px
            right -10px
            bottom -10px
            content ''
      .detail
        display flex
        flex 1
        height 100%
        align-items center
        background #F0F0F0
        .total,.price
          margin-left 15px
          label
            color #4D4D4D
            font-size $font-size-small
          span
            color #EB4747
            font-size $font-size-medium-m
            font-weight bold
      .btn
        display flex
        width 110px
        height 100%
        align-items center
        justify-content center
        color #fff
        background #EB4747
        font-size $font-size-medium-m


</style>



